.nav {
  z-index: 100;
  color: #edf2f7;
  background-color: #243447;
  width: 100%;
  height: 50px;
  display: block;
  position: fixed;
  top: 0;
  left: 0;
}

.nav .hamburger-button {
  background-color: #edf2f7;
  border-radius: 50%;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 3rem;
  height: 42px;
  margin: 4px;
  display: flex;
  position: absolute;
  right: 0;
}

.nav .hamburger-button .line {
  background-color: #243447;
  border-radius: 10%;
  width: 50%;
  height: 4px;
  margin: 6% 0;
}

.nav .nav__list {
  background-color: #edf2f7;
  width: 100%;
  list-style: none;
  transition: left .2s;
  position: absolute;
  top: 50px;
  left: 100%;
}

.nav .nav__list li {
  text-align: end;
  background-color: #243447;
  margin: 2px 0;
  padding: .4rem;
  font-size: 1.5rem;
}

@media screen and (width >= 640px) {
  .nav {
    height: 10%;
    overflow: hidden;
  }

  .nav .nav__list {
    background-color: unset;
    text-align: center;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 100%;
    display: flex;
    position: static;
  }

  .nav .nav__list li {
    margin: 0 10px;
    padding: 6px;
    display: inline-block;
  }

  .nav .nav__list li:after {
    content: "";
    background-color: #edf2f7;
    width: 100%;
    height: 2px;
    transition: transform .6s;
    display: block;
    transform: scale(0, 1);
  }

  .nav .nav__list li:hover {
    color: #243447;
    background-color: #b0bec5;
    transition: background-color .6s;
  }

  .nav .nav__list li:hover:after {
    background-color: #243447;
  }

  .nav .nav__list .active:after {
    transform: scale(1) !important;
  }

  .nav .hamburger-button {
    display: none;
  }
}

